/* index.vue */
.header {
  position: relative;
  width: 100%;
  height: 2.1rem;
  box-shadow: 0 0.1rem 0.1rem rgba(0, 0, 0, 0.4);
  background: #fff;
}
.header__content {
    position: absolute;
    bottom: 5px;
    right: 5px;
    font-size: .35rem;
}
.header__content--color {
      color: #f00;
}
.theme {
  width: 9rem;
  height: 4.5rem;
  margin: .4rem auto 0 auto;
}
.theme img {
    width: 100%;
    border-radius: 8px;
    box-shadow: 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.4);
}
.public {
  width: 94%;
  height: auto;
  margin: .5rem auto 0 auto;
}
.public > p {
    text-align: center;
    font-size: .5rem;
}
.public__item {
    width: 100%;
    height: 3rem;
    background: rgba(255, 204, 0, 0.2);
    margin: .5rem 0;
    box-shadow: 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.4);
}
.public__title {
    font-size: .4rem;
    font-weight: bold;
    margin-bottom: .1rem;
    text-align: center;
}
.public__content {
    text-align: center;
    font-size: .38rem;
}
.private {
  width: 94%;
  height: auto;
  margin: .5rem auto 0 auto;
}
.private__wrap {
    margin-bottom: .5rem;
    box-shadow: 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.4);
}
.private__content {
    width: 100%;
    height: 3rem;
    background: rgba(255, 204, 0, 0.2);
}
.private__img {
    display: block;
    width: 100%;
}
.button {
  width: 96%;
  margin: 0 auto .2rem auto;
}
* {
  padding: 0;
  margin: 0;
}
body {
  font-size: .6rem;
  overflow-x: hidden;
}
a {
  text-decoration: none;
}
.app {
  -webkit-transition: all 0.8s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 0.8s cubic-bezier(0.55, 0, 0.1, 1);
}
.outLeftInRight-enter,
.fadeInLeft-enter,
.fadeInLeft-leave {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.outLeftInRight-leave,
.fadeOutLeft-enter,
.fadeOutLeft-leave {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
}
.hNormal {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.hReverse {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
}
.vNormal {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.vReverse {
  -webkit-box-orient: vertical;
  -webkit-box-direction: reverse;
  -webkit-flex-direction: column-reverse;
          flex-direction: column-reverse;
}
.horizontal {
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.left {
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.right {
  -webkit-box-pack: end;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}
.hCenter {
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
.between {
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.around {
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.h-inherit {
  -webkit-box-pack: inherit;
  -webkit-justify-content: inherit;
          justify-content: inherit;
}
.top {
  -webkit-box-align: start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
.bottom {
  -webkit-box-align: end;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.vCenter {
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.v-baseline {
  -webkit-box-align: baseline;
  -webkit-align-items: baseline;
          align-items: baseline;
}
.v-stretch {
  -webkit-box-align: stretch;
  -webkit-align-items: stretch;
          align-items: stretch;
}
.center {
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}



